import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Button as MaterialButton,
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { Fragment, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { MdDelete, MdEdit } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import {
  clearErrors,
  deleteProduct,
  getAdminProducts,
} from "../../../../redux/actions/productAction";
import { DELETE_PRODUCT_RESET } from "../../../../redux/constants/productConstant";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import Loader from "../../../layout/Loader/Loader";
import MetaData from "../../../layout/MetaData/MetaData";
import SideBar from "../Dashboard/SideBar/SideBar";
import "./Products.css";

const Products = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const alert = useAlert();

  const { isAuthenticated, user } = useSelector((state) => state.user);
  const { error, products } = useSelector((state) => state.products);
  const {
    error: productDeleteError,
    isDeleted,
    loading,
  } = useSelector((state) => state.product);

  const [open, setOpen] = useState(false);

  const deleteProductToggle = () => {
    setOpen((old) => (old ? false : true));
  };

  const productDeleteHandler = (id) => {
    dispatch(deleteProduct(id));
  };

  const columns = [
    { field: "id", headerName: "Product Id", flex: 0.2 },
    { field: "name", headerName: "Name",type: "number",flex: 0.2 },
    {
      field: "stock",
      headerName: "Stock",
      type: "number",
      flex: 0.2,
    },
    {
      field: "price",
      headerName: "Price",
      type: "number",
      flex: 0.2,
    },
    {
      field: "actions",
      headerName: "Actions",
      flex: 0.2,
      type: "number",
      sortable: false,
      renderCell: (params) => {
        return (
          <Fragment>
            <Link to={`/admin/product/${params.getValue(params.id, "id")}`}>
              <MdEdit />
            </Link>

            <Button onClick={deleteProductToggle}>
              <MdDelete />
            </Button>
            <Dialog
              aria-labelledby="simple-dialog-title"
              open={open}
              onClose={deleteProductToggle}
            >
              <DialogTitle>Delete Product</DialogTitle>
              <DialogContent className="submitDailog">
                Do you want to Delete this product{" "}
                {params.getValue(params.id, "name")}
              </DialogContent>

              <DialogActions>
                <MaterialButton onClick={deleteProductToggle} color="primary">
                  Cancel
                </MaterialButton>
                <MaterialButton
                  onClick={() =>
                    productDeleteHandler(params.getValue(params.id, "id"))
                  }
                  color="secondary"
                >
                  Delete
                </MaterialButton>
              </DialogActions>
            </Dialog>
          </Fragment>
        );
      },
    },
  ];

  const rows = [];
  products &&
    products.forEach((product) => {
      rows.push({
        id: product._id,
        name: product.name,
        stock: product.stock,
        price: product.price,
      });
    });

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

    if (error) {
      alert.error(error);
      dispatch(clearErrors());
    }
    if (productDeleteError) {
      alert.error(productDeleteError);
      dispatch(clearErrors());
    }

    if (isDeleted) {
      alert.success("Product Deleted Successfully");
      dispatch({ type: DELETE_PRODUCT_RESET });
    }

    dispatch(getAdminProducts());
  }, [
    isAuthenticated,
    navigate,
    alert,
    user,
    dispatch,
    error,
    productDeleteError,
    isDeleted,
  ]);

  return (
    <Fragment>
      <MetaData title="All Products - Admin" />

      <div className="dashboard">
        <SideBar />

        <div className="productListContainer">
          <h1 id="productListHeading">All Products</h1>

          {loading ? (
            <Loader />
          ) : (
            <DataGrid
              columns={columns}
              rows={rows}
              pageSize={10}
              disableSelectionOnClick
              className="productListTable"
           
            />
          )}
        </div>
      </div>
    </Fragment>
  );
};

export default Products;
